body {
  font-family: Roboto, sans-serif;
}

@media screen and (min-width: 320px), (max-width: 480) {
  .container {
    min-width: 320px;
    max-width: 428px;
    margin: auto;
    padding: 0 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}







:root {
  --primary-brand-color: #000;
}

header {
  background: var(--primary-brand-color);
}

a {
  text-decoration: none;
}

li {
  list-style-type: none;
}

.header_container {
  justify-content: space-between;
  align-items: center;
  padding-top: 8px;
  padding-bottom: 8px;
  display: flex;
}

.logo {
  align-items: center;
  display: flex;
}

.logo_img {
  width: 30px;
  height: 24px;
}

.logo_text {
  display: none;
}

@media screen and (max-width: 767px) {
  .header_menu_btn {
    text-transform: uppercase;
    color: #b7b7b7;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    outline: none;
    font-size: 12px;
    font-weight: 500;
    line-height: 1.16;
    transition: color .25s cubic-bezier(.4, 0, .2, 1);
    display: block;
  }
}

.navbar_list {
  display: none;
}

#icon_moon, #icon_sun {
  width: 8px;
  height: 8px;
}

.mobile_menu_container {
  background-color: var(--primary-brand-color);
  height: 100%;
  z-index: 100;
  width: calc(100% - 116px);
  border-radius: 0 8px 8px 0;
  padding: 20px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(-100%);
}

@media screen and (min-width: 768px) {
  .navbar_list {
    display: block;
  }
}















